// mobile menu
(function initMobileMenu() {
  const hamburgerBtn = document.querySelector(".hamburger");
  const mobileWindow = document.querySelector(".mobile-menu__inner");
  const overlay = document.querySelector(".overlay");
  const html = document.documentElement;

  hamburgerBtn.addEventListener("click", function () {
    html.classList.add("no-scroll");
    this.classList.toggle("is-active");
    mobileWindow.classList.toggle("active-mobile");
    overlay.classList.toggle("active-mobile");
  });

  overlay.addEventListener("click", () => {
    hamburgerBtn.classList.remove("is-active");
    mobileWindow.classList.remove("active-mobile");
    overlay.classList.remove("active-mobile");
    html.classList.remove("no-scroll");
  });

}) ();

// modal window

(function initModalWindow() {
  const modalBtn = document.getElementById("modal-btn");
  const modalWindow = document.querySelector(".modal");
  const overlay = document.querySelector(".overlay");
  const modalCloseBtn = document.querySelector(".modal__close-wrapper");
  const html = document.documentElement;

  modalBtn.addEventListener("click", () => {
    html.classList.toggle("no-scroll");
    modalWindow.classList.toggle("active-modal");
    overlay.classList.toggle("active-modal");
    modalCloseBtn.classList.remove("active-modal");
  });

  modalCloseBtn.addEventListener("click", () => {
    modalWindow.classList.remove("active-modal");
    overlay.classList.remove("active-modal");
  });

  overlay.addEventListener("click", () => {
    modalWindow.classList.remove("active-modal");
    overlay.classList.remove("active-modal");
    html.classList.remove("no-scroll");
  });

}) ();